<template>
	<!-- 绑定手机号 -->
	<view class="">
		<!-- 导航栏 -->
		<view class="width-000 ">
			<commonTitle :defaultSty="false" :isShowIcon="true" :center="true" title="绑定手机号"></commonTitle>
		</view>
		<!-- 导航栏 -->
		<divider></divider>
		<view class="">
			<view class="px-3 d-flex a-center j-sb" style="height: 85rpx;border-bottom: 0.5px solid rgb(156, 66, 126);">
				<view class="" style="font-size: 27rpx;font-family: PingFang SC;font-weight: bold;color: #FFFFFF;">
					手机号
				</view>
				<view class="height-000" style="width: 60%;">
					<input type="number" maxlength="11" placeholder-class="phcolor" v-model="mobile" placeholder="请输入手机号" class="text-right height-000" style="font-size: 27rpx;font-family: PingFang SC;font-weight: 400;color: #FFFFFF;" />
				</view>
			</view>
			<view class="pl-3 d-flex a-center j-sb" style="height: 85rpx;border-bottom: 0.5px solid rgb(156, 66, 126);">
				<view class="height-000" style="width: 60%;">
					<input type="number"   v-model="code" placeholder-class="phcolor" placeholder="请输入验证码" class="text-left height-000" style="width: 80%; font-size: 27rpx;font-family: PingFang SC;font-weight: 400;color: #FFFFFF;" />
				</view>
				<view @tap="getCode" class="d-flex a-center j-center" style="width: 220rpx;height: 84rpx;background: #803667;">
					<view class="" style="font-size: 27rpx;font-family: PingFang SC;font-weight: 400;color: #FFFFFF;">
						{{verifyTime == 60 ?'获取验证码':"已发送"+verifyTime}}
					</view>
				</view>
				
			</view>
			
			<view @tap="bind" class="d-flex a-center j-center px-3 " style="margin-top: 300rpx;">
				<view class="d-flex j-center a-center width-000" style="height: 80rpx;background: #FF2F68;border-radius: 10rpx;">
					<view class="" style="font-size: 32rpx;font-family: PingFang SC;font-weight: bold;color: #FFFFFF;">
						确认绑定
					</view>
				</view>
			</view>
		
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				mobile: '',
				code: '',
				verifyTime: 60,
				isEendCode: false
			}
		},
		methods:{
			// 绑定手机号
			bind(){
				var isMobile = this.$U.reg.checkPhone(this.mobile)
				if (isMobile) {
					return
				}
				var isEmpty = this.$U.reg.isEmpty(this.code, '验证码为空')
				
				if (isEmpty) {
					return
				}
				console.log('aaaaaaa')
				
			},
			// 获取验证码
			async getCode() {
				var isMobile = this.$U.reg.checkPhone(this.mobile)
				if (isMobile) {
					return
				}
				if (!this.isEendCode) {
					// var data = await this.sms_codes()
					var data = {
						Code:200
					}
					if (data.Code == '200') {
						this.isEendCode = true
						var time = setInterval(() => {
							if (this.verifyTime == 0) {
								this.verifyTime = 60
								this.isEendCode = false
								clearInterval(time)
								return
							}
							this.verifyTime--
						}, 1000)
			
					} else {
						uni.showToast({
							title: '验证码发送失败',
							icon: "none"
						})
					}
			
				}
			
			
			
			},
		}
		
	}
</script>

<style>
	page {
		background: #5B2549;
	}
	.phcolor{
		color: #fff;
		opacity: 0.8;
	}
	.uni-input-placeholder{
		color: #fff;
	}
	input::-webkit-input-placeholder {
		color: #fff;
	}

	input::-moz-placeholder {
		/* Mozilla Firefox 19+ */
		color: #fff;
	}

	input:-moz-placeholder {
		/* Mozilla Firefox 4 to 18 */
		color: #fff;
	}

	input:-ms-input-placeholder {
		/* Internet Explorer 10-11 */
		color: #fff;
	}
</style>
